<template>
  <div class="column">
    <!-- <List :list="$store.state.column" dataName="dataName"></List> -->
    <ul>
      <li v-for="item in $store.state.column" :key="item._id">
        <router-link href="javascript:;" :to="'/detail/'+item._id">
          <h2>{{item._id}}/{{item.title}}</h2>
          <!-- <p>{{item.detail}}</p> -->
        </router-link>
        <img src="../assets/img/xing.png" @click="ADD_ITEM(item)">
      </li>
    </ul>
  </div>
</template>
<script>
import List from "../components/List";
import {mapActions} from 'vuex'
export default {
  components: { List },
  mounted() {
    this.$store.dispatch("UPDATE_COLUMN");
  },
  methods:mapActions([
    'ADD_ITEM'
  ])
};
</script>
<style scoped>
.column {
  padding-top: 0.7rem;
}
</style>

